<template>
  <div class="songer">
    <div class="head-bg" :style="{ backgroundImage: `url(${songerImg})` }">
        <div class="introduce-text">
            <h2>{{artist.name}}</h2>
        </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      briefDesc: "",
      songerImg: "",
      artist:""
    };
  },
  created() {
    this.axios("/artists?id=5781").then((res) => {
    //   console.log("res :>> ", res);
      this.briefDesc = res.data.artist.briefDesc;
      this.songerImg = res.data.artist.img1v1Url;
      this.artist = res.data.artist
    });
    // this.axios("/artist/desc?id=5781").then((res) => {
    //   console.log("res :>> ", res);
    // });
  },
};
</script>

<style lang="scss" scoped>
.head-bg {
  background-size: cover;
  background-repeat: no-repeat;
  overflow: hidden;
  position: relative;
  height: 360px;
  &::before {
    position: absolute;
    content: "";
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.3);
    z-index: 1;
  }
  .introduce-text {

  }
}
</style>
